.about-us-header {
  padding: 130px 0;
  margin-bottom: 70px;
 
}
.about-us-content {

  .images-achieve {
    margin-bottom: 110px;
  }
  img{
    width: 100%;
  }

  .main-heading {
    text-align: left;
  }
  .condition-about {
    p {
      font-size: 20px;
      color: $paragraph-color;
    }
  }
}
.achieve-image-left {
  position: relative;
  bottom: 65px;
}
.about-us-text {
  p {
    color: $paragraph-color;
    margin-bottom: 30px;
    line-height: 34px;
    font-size: 20px;
    letter-spacing: -0.03em;

  }
}
.skilled-employee {
  padding-top: 230px;
 

  .main-heading {
    text-align: left;
    line-height: 60px;
    max-width: 100%;
  }
  .condition-about {
    p {
      font-size: 18px;
      margin-bottom: 30px;
      line-height: 20px;
      color: $paragraph-color;
      line-height: 25px;
    }
  }
}
.image-skill-left {
  position: relative;
  float: right;
  top: 30px;
}
.image-skill-right {
  position: relative;
  bottom: 80px;
}
.skill-text {
  max-width: 495px;
  margin: auto;
  
}
.about-us-footer {
  padding: 100px 0;
  
  
}
.team-profile {
  margin-bottom: 70px;
  text-align: center;

  img {
    width: 100%;
    margin-bottom: 30px;
  }

  h3 {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  span {
    color: $paragraph-color;
  }
}
.part-team {
  max-width: 240px;
  margin: auto;
  text-align: center;
  padding: 120px 0;
  h5 {
    margin-bottom: 30px;
    font-size: 22px;
    font-weight: bold;
  }
  a {
    color: $main-color;
    border-bottom: 2px solid $main-color;
    padding-bottom: 10px;
    &:hover {
      font-weight: bold;
      border-bottom: 2px solid #000000;
    }
  }
}
